@import "./variables.scss";

body {
  margin: 0;
  padding: 0;
  font-family: "Lato-Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  color: white;

  a {
    color: white;
  }
}

.App {
  // iOS height fix: https://css-tricks.com/the-trick-to-viewport-units-on-mobile/
  height: calc(var(--vh, 1vh) * 100);

  .main {
    height: calc(calc(var(--vh, 1vh) * 100) - #{$header-height});
    display: grid;
    grid-template-columns: $nav-closed-width 1fr;
    position: relative;
    overflow: hidden;

    .sections-wrapper {
      z-index: 2; // to get it on same plane as active nav-tab
      position: relative;
      height: inherit;
      overflow-y: scroll;
      overflow-x: hidden;
      -webkit-overflow-scrolling: touch;
      outline: none;
    }
  }
}

@mixin main_color($color) {
  background-color: get-color($name: $color);
  .subtitle {
    color: get-color($name: $color);
  }
  .label-group {
    background-color: get-color($name: $color);
  }
  [class^="category"]:not(.selected) {
    color: get-color($name: $color, $variant: "tertiary");
  }
  &.secondary {
    background-color: get-color($name: $color, $variant: "secondary");
  }
  .bar-group {
    border-color: get-color($name: $color);
  }
  .bar-fill {
    background-color: get-color($name: $color, $variant: "secondary");
  }
  .label-subset {
    color: get-color($name: $color, $variant: "tertiary");
  }
  .dot {
    background-color: get-color($name: $color, $variant: "secondary");
  }
  hr {
    background: get-color($name: $color, $variant: "tertiary");
  }
}

.orange {
  @include main_color("orange");
  .dot {
    background-color: get-color($name: "orange", $variant: "tertiary");
  }
}
.yellow {
  @include main_color("yellow");
  [class^="category"]:not(.selected) {
    color: get-color($name: "yellow", $variant: "quaternary");
  }
  .label-subset {
    color: get-color($name: "yellow", $variant: "quaternary");
  }
  hr {
    background: get-color($name: "yellow", $variant: "quaternary");
  }
}
.green {
  @include main_color("green");
}
.teal {
  @include main_color("teal");
}
.blue {
  @include main_color("blue");
}
.pink {
  @include main_color("pink");
}
.purple {
  @include main_color("purple");
}

$animation-duration: 1000ms;
$text-animation-duration: 500ms;
$text-animation-delay: 500ms;

// animations
// CARD TITLE TEXT ANIMATION
.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
  animation-duration: $animation-duration;
  animation-delay: 0ms;
  animation-iteration-count: 1;
  opacity: 1;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 75px, 0);
    transform: translate3d(0, 75px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.growUp [class^="label"],
.growOut [class^="label"] {
  animation-name: textFade;
  animation: textFade 500ms linear $text-animation-delay backwards;
}

// VERTICAL BAR ANIMATION
.growUp {
  animation-name: growUp;
  animation-duration: $animation-duration;
  animation-delay: 0ms;
  animation-iteration-count: 1;
  opacity: 1;
  display: inline-block;
}

@keyframes growUp {
  0% {
    -webkit-transform-style: preserve-3d;
    transform-origin: 50% 100%;
    transform: scale3d(1, 0, 1);
  }

  to {
    -webkit-transform-style: preserve-3d;
    transform-origin: 50% 100%;
    transform: scale3d(1, 1, 1);
  }
}

// HORIZONTAL BAR ANIMATION
.growOut {
  animation-name: growOut;
  animation-duration: $animation-duration;
  animation-delay: 0ms;
  animation-iteration-count: 1;
  opacity: 1;
  display: inline-block;
}
@keyframes growOut {
  0% {
    -webkit-transform-style: preserve-3d;
    transform-origin: 0%;
    transform: scale3d(0, 1, 1);
  }

  to {
    -webkit-transform-style: preserve-3d;
    transform-origin: 0%;
    transform: scale3d(1, 1, 1);
  }
}

@keyframes textFade {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: $mobile-breakpoint) {
  .App {
    .main {
      grid-template-columns: $mobile-nav-closed-width 1fr;
      height: calc(var(--vh, 1vh) * 100);
    }
  }
}
