@import "../../styling/variables.scss";

.WordCloud {
  width: 100%;
  height: $chart-min-height + $toggle-height;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-size: 1em;
  padding-top: 5px;
  align-self: center;

  .speech-bubble {
    height: 1em;
    position: relative;
    background: white;
    border-radius: 0.4em;
    color: get-color();
    font-family: "ASAP-Regular";
    padding: 5px;
    box-shadow: 1px 4px 9px -2.75px $drop-shadow-color;
    cursor: default;
  }

  .speech-bubble:hover {
    transform: scale(1.2);
    z-index: 1;
  }

  .speech-bubble:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 40%;
    width: 0;
    height: 0;
    border: 0.5em solid transparent;
    border-top-color: white;
    border-bottom: 0;
    border-left: 0;
    fill: none;
    margin-left: -0.8em;
    margin-bottom: -0.5em;
  }
}

@media (max-width: $mobile-breakpoint) {
  .WordCloud {
    height: $mobile-chart-min-height + $mobile-toggle-height;

    .speech-bubble {
      height: 1em;
      font-size: 0.75em;
    }
  }
}
