@import "../../styling/variables.scss";

.VerticalBarChart {
  display: grid;
  height: 100%;
  margin: auto;

  &.vertical-many {
    width: 90%;
    display: grid;
    grid-auto-columns: minmax(60px, 1fr);
    grid-template-rows: 100%;
    // grid-auto-rows: 80% 20%;
    .bar-group {
      grid-row: 1;
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: 80% 20%;
      justify-items: center;

      .bar-dots,
      .bar-measure {
        grid-row: 1/2;
      }
      .bar-dots {
        height: 100%;
        overflow: hidden;
      }
      .label-measure {
        grid-row: 2;
        top: 5%;
      }
    }
  }
  &.vertical-two,
  &.subset {
    grid-template-columns: 50% 50%;
    width: 90%;
  }

  .bar-group {
    position: relative;

    [class^="bar"] {
      position: absolute;
      width: 50px;
      transition: height 500ms ease;
      border-radius: 10px;
      border-style: solid;
      border-color: inherit;
      bottom: -$vert-bar-border;
    }

    [class^="label"] {
      position: absolute;
    }

    .bar-measure {
      background-color: #ffffff;
    }

    .label-measure {
      font-size: 16px;
      font-family: Asap-Regular;
      bottom: 0;
    }

    .label-data {
      font-size: 14px;
      font-family: Lato-Black;
      width: 50px;
      text-align: center;
      margin-top: 7px;
      color: get-color($name: "orange");
      &.mid {
        margin-top: 4px;
      }
      &.small {
        display: none;
      }
    }

    .label-subset {
      font-family: Asap-Regular;
    }

    &:not(.vertical-many) {
      &.pos-left {
        .bar-measure,
        .bar-fill,
        .bar-dots {
          left: calc(100% - 58px);
          bottom: -$vert-bar-border;
        }
        .label-measure,
        .label-subset {
          width: calc(100% - 68px);
          text-align: right;
        }
      }

      &.pos-right {
        .bar-measure,
        .bar-fill,
        .bar-dots {
          left: 2px;
          bottom: -$vert-bar-border;
        }
        .label-measure,
        .label-subset {
          left: 65px;
          width: calc(100% - 65px);
          text-align: left;
        }
      }
    }

    &.subset {
      [class^="bar"] {
        border: none;
      }
      .bar-fill {
        height: 100%;
      }
    }

    &.vertical-two,
    &.vertical-many {
      .dot {
        display: block;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        margin: 0px 20px 10px 20px;
      }
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .VerticalBarChart {
    &.vertical-two,
    &.vertical-many,
    &.subset {
      width: 100%;
    }
    .bar-group {
      .label-data {
        &.mid {
          margin-top: 1px;
        }
      }
    }
  }
}
