@import "../../styling/variables.scss";

.HorizontalBarChart {
  height: 100%;
  width: 100%;
  display: grid;
  grid-auto-rows: 1fr;
  grid-template-rows: 5fr repeat(3, 12fr) 5fr repeat(3, 12fr) 5fr 12fr;

  .bar-group {
    position: relative;
    display: grid;

    // this applies the same border style to both .dots-group and .bar for spacing / centering reasons
    div {
      border-radius: 10px;
      border-style: solid;
      border-width: $vert-bar-border;
      border-color: inherit;
    }

    .bar-dots {
      position: absolute;
      height: 80%;
      bottom: 0px;
      left: -2px;
      display: grid;
      align-content: center;
    }
    .dot {
      grid-row: 1;
      display: inline-block;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      margin: auto 5px;
    }

    .bar-measure {
      position: absolute;
      height: 80%;
      bottom: 0px;
      left: -2px;
      display: grid;
      grid-auto-columns: auto 45px;
      align-content: center;
      transition: width 500ms ease;
      background-color: #ffffff;
      &.truncated {
        &:hover {
          width: 100% !important;
          .label-measure {
            .measure {
              display: none;
            }
          }
          .measure-hover {
            pointer-events: none;
            opacity: 1;
            display: inline;
          }
          .label-data {
            color: white;
            transition: color 150ms;
          }
        }
      }
    }
    .label-measure {
      grid-row: 1;
      font-size: 14px;
      font-family: Lato-Regular;
      text-align: left;
      color: get-color($name: "orange");
      border: none;
      border-radius: 0px;
      left: 10px;
      margin-left: 10px;
      height: 1.2em;
      overflow: hidden;
      width: 100%;
      .measure-hover {
        opacity: 0;
        border: inherit;
        display: none;
      }
    }

    .label-data {
      grid-row: 1;
      font-size: 14px;
      font-family: Lato-Black;
      border: none;
      text-align: right;
      margin: 0px 10px;
      color: get-color($name: "orange");
    }
  }
  .label-group-div {
    position: relative;
    height: 10px;
    hr {
      height: 2px;
      border: none;
    }
    .label-group {
      position: absolute;
      height: 10px;
      right: 0;
      top: 0;
      padding: 0 5px;
      font-size: 12px;
      font-family: Lato-Regular;
      text-align: right;
      z-index: 1;
    }
  }
}
