@import "../../styling/variables.scss";

.Card {
  position: relative;
  height: $card-height;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 1em;
  text-align: center;
  align-items: center;
  padding: 30px;
  border-bottom: 2px white dashed;

  &:last-child {
    border-bottom: none;
    margin-bottom: 2px;
  }

  .chart-wrapper {
    align-items: start;
    align-self: start;
    display: grid;
    height: inherit;
    position: relative;
    box-sizing: border-box;

    &.annotation {
      grid-auto-rows: 0 0 auto;
    }

    .graph-title {
      text-align: left;
      padding: 0 20px 0 10px;

      span {
        font-family: Lato-Regular;
        font-size: 16px;
      }
    }

    .Toggle {
      max-height: $toggle-height + 20;
      min-height: $toggle-height;
    }

    .VerticalBarChart,
    .HorizontalBarChart {
      max-height: $card-height;
      min-height: $chart-min-height;
    }

    .annotation {
      height: 100%;
      padding: 0 1em;
      color: white;
      border-left: solid 2px;
      font-family: "Asap-Regular";
      overflow-x: hidden;
      overflow-y: scroll;

      .content {
        text-align: left;
        overflow-y: visible;
        font-size: 1.5em;

        .highlight {
          font-family: ASAP-Regular;
          .bold {
            font-family: ASAP-Bold;
          }
          &:not(:last-child) {
            padding-bottom: 1em;
          }
        }

        .text {
          font-size: 16px;
          font-family: Lato-Regular;
          padding-bottom: 1em;
          .bold {
            font-family: Lato-Black;
          }
        }

        .list {
          padding-bottom: 1em;
          font-family: Lato-Regular;
          font-size: 16px;
          display: list-item;
          margin-left: 25px;
          .bold {
            font-family: Lato-Black;
          }
        }
      }
    }
  }

  &.no-toggle {
    .chart-wrapper {
      &.annotation {
        grid-auto-rows: 0 0 auto;
      }

      &:not(.annotation) {
        grid-auto-rows: $title-height 0 $chart-min-height + $toggle-height;
      }
    }
  }

  .pos-1 {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
  }

  .pos-2 {
    grid-column-start: 3;
    grid-column-end: end;
    grid-row-start: 1;
  }
}

@media screen and (max-width: $medium-breakpoint) {
  .Card {
    .chart-wrapper {
      .VerticalBarChart,
      .HorizontalBarChart {
        max-height: $medium-card-height;
        min-height: $medium-chart-min-height;
        [class^="label"] {
          font-size: 0.75em;
          overflow-wrap: break-word;
        }
      }

      .graph-title {
        height: auto;
      }

      .Toggle {
        z-index: 1;
        div {
          font-size: 0.75em;
          overflow-wrap: break-word;
        }
      }

      &:not(.annotation) {
        grid-auto-rows: auto minmax($mobile-toggle-height, auto) 1fr;
      }
    }
  }
}

@media screen and (max-width: $mobile-breakpoint) {
  .Card {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-row-gap: 1em;
    padding: 1em;
    height: unset;

    .pos-1 {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
    }

    .pos-2 {
      grid-row-start: 3;
      grid-row-end: end;
      grid-column-start: 1;
    }

    .chart-wrapper {
      .VerticalBarChart,
      .HorizontalBarChart {
        max-height: $mobile-card-height;
        min-height: $mobile-chart-min-height;
      }

      .graph-title {
        height: auto;
        padding: 10px 0px;
      }
    }
  }
}
