@import "../../styling/variables.scss";

.PieChart {
  display: grid;
  align-self: center;

  svg {
    overflow: visible;
  }

  .pie-path {
    stroke: white;
    stroke-width: 3px;
    stroke-linejoin: round;
  }

  .label-group {
    transition-property: transform;
    transition-duration: 500ms;
    [class^="label"] {
      pointer-events: none;
      transition: opacity 300ms, width 200ms, height 200ms;
      opacity: 0;
    }
    &.show {
      [class^="label"] {
        opacity: 1;
      }
    }
  }

  .label-data-rect {
    opacity: inherit;
    fill: #ffffff;
    stroke: white;
    stroke-width: 7px;
  }
  .label-data-text {
    opacity: inherit;
    font-size: 14px;
    fill: get-color($name: "orange");
    &.left {
      text-anchor: end;
    }
    tspan.value {
      font-family: Lato-Black;
    }
    tspan.label {
      font-family: ASAP-Regular;
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .PieChart {
    transform: scale(0.9) translate(5%, -5%);
    height: 90%;
    .label-data-rect {
      filter: none;
    }
  }
}
