@import "../../styling/variables.scss";

.Navigation {
  width: $nav-closed-width;
  height: inherit;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(8, 1fr);
  cursor: pointer;

  &.isOpen {
    width: $nav-open-width;
    z-index: 3;
    box-shadow: 2px 0px 10px 0px $drop-shadow-color;

    span {
      pointer-events: none;
      margin: auto 10px;
    }

    .nav-tab {
      grid-template-columns: 30px 1fr;
      grid-column-gap: 7px;
    }
  }

  .nav-tab {
    text-transform: uppercase;
    transition: box-shadow, color 300ms, 300ms;
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    font-family: "Lato-Black";
    font-size: 14px;
    text-align: center;

    img {
      transition: transform 300ms;
      margin-left: 10px;
      width: 30px;
    }

    &.secondary {
      // box-shadow: inset -1px 0px 5px $drop-shadow-color;
      &:hover {
        img {
          transform: scale(1.3);
        }
      }
    }

    // selected tab
    &:not(.secondary) {
      z-index: 2;
      box-shadow: 0px 0px 10px $drop-shadow-color;
      // border-left: 3px solid #ffffff;
    }
  }
  &:focus {
    outline: none;
  }
}
@media (max-width: $mobile-breakpoint) {
  .Navigation {
    width: $mobile-nav-closed-width;
    .nav-tab {
      img {
        margin-left: 7px;
      }
    }
  }
}
